<template>
	<view class="nav">
		<view class="nav-img">
			<image class="img" :src="menulist.large_img"></image>
		</view>
		<view class="nav-content">
			<view class="nav-bottom-top">
				<view class="nav-name">
					<view class="name">{{menulist.name}}</view>
					<view class="enname">{{menulist.enname}}</view>
				</view>
				<view class="price">￥{{menulist.price}}</view>
			</view>
			<view class="nav-select" v-for="(item,index) in newtypelist" :key="index">
				<view class="nav-tem">{{item.title}}</view>
				<view class="tem" v-for="(v,i) in item.typelist" :key='i'>
					<view class="ice" @click="typeselect(item,i,index)" :class="item.typeid===i?'selectT':''">
						<view>{{v}}</view>
					</view>
				</view>
			</view>
			<view class="nav-count">
				<view class="select">选择数量</view>
				<view class="bnt">
					<view class="down" :style="count==1?'':'background-color: #0c34ba color:#ffffff'" @click="down()">-
					</view>
					<view class="count">{{count}}</view>
					<view class="add" @click="add()">+</view>
				</view>
			</view>
			<view class="nav-detail">
				<view class="title">商品描述</view>
				<view class="content" v-for="(item,index) in menulist.desc">{{index+1}}.{{item}}</view>
			</view>
		</view>
		<view class="nav-bottom">
			<view class="nav-shop" :style="addshop===true?'color: #0c34ba':''">
				<text class="iconfont icon-shangpingouwudai2"></text>
				<view class="shop">购物袋</view>
				<view class="count">1</view>
			</view>
			<view @click="coll()" class="nav-collect" :style="collect===true?'color: #0c34ba':''">
				<text class="iconfont icon-xin"></text>
				<view class="collect">收藏</view>
			</view>
			<view class="nav-box">
				<view class="addshop" @click="adshop()">加入购物袋</view>
				<view class="buy">立即购买</view>
			</view>
		</view>
	</view>
</template>

<script>
	import iconfont from "../../static/style/iconfont.css"
	import request from "../request/request.js"
	export default {
		data() {
			return {
				menulist: "",
				typedesc: '',
				count: 1,
				collect: true,
				shopcount: 1,
				addshop: '',
				newtypelist: [],
				typeindex: 0,
				listrule: '',
				slistrule: []
			}
		},
		methods: {
			getRequest() {
				const value = uni.getStorageSync('menulist')
				this.menulist = value
				console.log(this.menulist)
				let typelist = ["cream", "milk", "sugar", "tem"]
				let cou = 0
				typelist.forEach(v => {
					if (this.menulist[v] === "") {
						return
					}
					let str = this.menulist[v].split("/")
					console.log(str)
					let des = `${v}_desc`
					let aa = {
						title: this.menulist[des],
						typeid: cou,
						typelist: str
					}
					this.newtypelist.push(aa)

				})
				console.log(this.newtypelist)
				let munedesc = (this.menulist.desc)
				this.menulist.desc = munedesc.split('\n')
				//增加listrule规格
				// for(var n=0;n<this.newtypelist.length;n++){
				// 	if(!this.listrule){
				// 		this.listrule=this.listrule+this.newtypelist[n].typelist[0]
				// 	}
				// 	else{
				// 		this.listrule=this.listrule+'/'+this.newtypelist[n].typelist[0]
				// 	}
				// }
				for (var n = 0; n < this.newtypelist.length; n++) {
					const a = this.newtypelist[n].typelist[0]
					this.slistrule.push(a)
				}
			},
			down() {
				if (this.count == 1) {
					return
				} else {
					this.count -= 1
				}
			},
			add() {
				this.count += 1
			},
			//收藏
			coll() {
				if (this.collect === false) {
					this.collect = true
				} else {
					this.collect = false
				}
			},
			//加入购物袋
			adshop() {
				this.addshop = true;
				const token = uni.getStorageSync("token")
				request({
					url: "/addShopcart",
					method: "POST",
					data: {
						pid: this.menulist.pid,
						count: this.count,
						rule: this.listrule,
						tokenString: token
					},
					success:(res)=> {
						console.log(res)
					}
				})
			},
			//规格
			typeselect(item, i, index) {
				this.slistrule[index] = item.typelist[i]
				item.typeid = i
				this.typeindex = i
				this.listrule=''
				for (var n = 0; n < this.slistrule.length; n++) {
					if(!this.listrule){
						this.listrule =this.slistrule[0]
					}
					else{
						this.listrule =this.listrule+'/'+this.slistrule[n]
					}
						
				}
				console.log(this.listrule)
			}
		},
		onLoad() {
			this.getRequest()
		}

	}
</script>

<style lang="less" scoped>
	.nav {
		font-size: 25rpx;
		background-color: #f7f7f7;

		.nav-img {
			.img {
				width: 746rpx;
				height: 600rpx;
			}
		}

		.nav-content {
			position: relative;
			top: -70rpx;
			margin: 0 50rpx 0 50rpx;
			padding: 30rpx 0 30rpx 0;
			background-color: #FFFFFF;
			border-radius: 30rpx;

			.nav-bottom-top {
				margin-top: 30rpx;
				display: flex;
				justify-content: space-between;
				align-items: center;

				.nav-name {
					margin-left: 30rpx;

					.name {
						color: #757171;
						font-size: 40rpx;
						font-weight: bold;
					}

					.enname {
						color: #999999;
					}
				}

				.price {
					color: #0c34ba;
					margin-right: 30rpx;
					font-weight: bold;
				}
			}

			.nav-select {
				display: flex;
				align-items: center;

				.nav-tem {
					margin: 20rpx;
					width: 100rpx;
				}

				.tem {
					display: flex;
					font-size: 20rpx;

					.ice {
						width: 120rpx;
						height: 50rpx;
						text-align: center;
						line-height: 50rpx;
						color: #646566;
						background-color: #e8e8e8;
						border-radius: 50rpx;
						margin-left: 20rpx;
					}

					.selectT {
						color: #FFFFFF;
						background-color: #0c34ba;
					}
				}
			}


			.nav-count {
				margin: 30rpx;
				display: flex;
				justify-content: space-between;
				border-top: 2rpx #C0C0C0 solid;
				border-bottom: 2rpx #C0C0C0 solid;
				padding: 30rpx 0 30rpx 0;

				.select {}

				.bnt {
					display: flex;
					margin-right: 30rpx;
					align-items: center;

					.down {
						width: 30rpx;
						height: 30rpx;
						text-align: center;
						line-height: 30rpx;
						border: 2rpx solid #999999;
						border-radius: 30rpx;
					}

					.count {
						width: 50rpx;
						text-align: center;
					}

					.add {
						width: 30rpx;
						height: 30rpx;
						text-align: center;
						line-height: 30rpx;
						border-radius: 30rpx;
						background-color: #0c34ba;
						color: #FFFFFF;
					}
				}
			}

			.nav-detail {
				margin: 30rpx;

				.title {}

				.content {
					color: #999999;
				}
			}
		}

		.nav-bottom {
			display: flex;
			justify-content: space-between;
			background-color: #FFFFFF;
			margin: 0 30rpx 0 30rpx;
			padding: 10rpx 0 10rpx 0;
			position: sticky;
			bottom: 0;

			.iconfont {
				margin-left: 30rpx;
				font-size: 40rpx;
			}

			.nav-shop {
				width: 100rpx;
				height: 90rpx;
				position: relative;
				color: #646566;

				.shop {
					text-align: center;
				}

				.count {
					width: 30rpx;
					height: 30rpx;
					font-size: 20rpx;
					text-align: center;
					background-color: #0c34ba;
					top: -10rpx;
					right: 25rpx;
					color: #FFFFFF;
					position: absolute;
					border-radius: 30rpx;
				}
			}

			.nav-collect {
				margin-left: 10rpx;
				width: 100rpx;
				height: 90rpx;
				color: #646566;

				.collect {
					text-align: center;
				}
			}

			.nav-box {
				display: flex;
				color: #FFFFFF;

				.addshop {
					text-align: center;
					line-height: 90rpx;
					width: 240rpx;
					background-color: #6a91ec;
					border-radius: 100rpx 0 0 100rpx;
				}

				.buy {
					text-align: center;
					line-height: 90rpx;
					width: 240rpx;
					background-color: #0c34ba;
					border-radius: 0 100rpx 100rpx 0;
				}
			}
		}
	}
</style>
